// 商品管理相关接口
import axios from '../fetch'
import qs from 'qs'
// 商品列表
export const productPage = params => {
  return axios.get('/admin/store/product/page', { params: params,
    paramsSerializer: function(params) {
      return qs.stringify(params, { arrayFormat: 'repeat' })
    }
  })
}
// 商品下架
export const productOffShelve = params => { return axios.post('/admin/store/product/offShelve', params) }

// 商品待审核
export const productToCheck = params => { return axios.post('/admin/store/product/tocheck', params) }

// 商品放入回收站
export const productRecyleDelete = params => { return axios.post('/admin/store/product/recyle', params) }
// 商品还原
export const productRestore = params => { return axios.post('/admin/store/product/restore', params) }

// 商品删除
export const productDelete = params => { return axios.post('/admin/store/product/delete', params) }

// 获取商品详细信息

export const productGet = params => { return axios.get('/admin/store/product/get', { params: params }) }

// 商品新增
export const productSave = params => { return axios.post('/admin/store/product/save', params) }

export const productUpdate = params => { return axios.post('/admin/store/product/update', params) }

// 新增规格值
export const customStandard = params => { return axios.post('/admin/store/typeattrvalue/save', params) }

// 商品新增自定义规格
export const customStandardItem = params => { return axios.post('/admin/store/producttypeattr/saleSave', params) }

// 商家自定删除自定义规格项
export const customStandardDelete = params => { return axios.post('/admin/store/typeattrvalue/delete', params) }

// 通过商品类型获取到商品属性信息
export const customAttribute = params => { return axios.post('/admin/store/producttypeattr/nSaleSave', params) }
// 验证货号code唯一
export const ckItemNumber = params => { return axios.get('/admin/store/product/ckItemNumber', { params: params }) }
// 商品Sku信息删除
export const productSkuDelete = params => { return axios.post('/admin/store/productSku/delete', params) }

// 关联版式列表
export const productFormatList = params => { return axios.get('/admin/store/producttemplate/list', { params: params }) }
// 关联版式列表
export const productFormatPage = params => { return axios.get('/admin/store/producttemplate/page', { params: params }) }

// 关联版式列表删除
export const productFormatDelete = params => { return axios.post('/admin/store/producttemplate/delete', params) }

// 关联版式列表新增
export const productFormatSave = params => { return axios.post('/admin/store/producttemplate/save', params) }

// 关联版式列表修改
export const productFormatUpdate = params => { return axios.post('/admin/store/producttemplate/update', params) }

// 关联版式列表获取
export const productFormatGet = params => { return axios.get('/admin/store/producttemplate/get', { params: params }) }

// 卡密模板列表
export const productVirtualCardtpl = params => { return axios.get('/admin/store/productvirtualcardtpl/page', { params: params }) }

// 卡密模板新增
export const productVirtualCardtplSave = params => { return axios.post('/admin/store/productvirtualcardtpl/save', params) }

// 卡密模板详情
export const productVirtualCardtplGet = params => { return axios.get('/admin/store/productvirtualcardtpl/get', { params: params }) }

// 卡密模板修改
export const productVirtualCardtplUpdate = params => { return axios.post('/admin/store/productvirtualcardtpl/update', params) }

// 卡密模板删除
export const productVirtualCardtplDelete = params => { return axios.post('/admin/store/productvirtualcardtpl/delete', params) }

// 卡密数据-->列表分页
export const productVirtualCardDataPage = params => { return axios.get('/admin/store/productvirtualcarddata/page', { params: params }) }

// 卡密数据-新增
export const productVirtualCardDataSave = params => { return axios.post('/admin/store/productvirtualcarddata/save', params) }

// 卡密数据-删除
export const productVirtualCardDataDelete = params => { return axios.post('/admin/store/productvirtualcarddata/delete', params) }

// 卡密数据-详情
export const productVirtualCardDataGet = params => { return axios.get('/admin/store/productvirtualcarddata/get', { params: params }) }



