// 商城-角色相关接口
import axios from './../fetch'

// 角色列表-分页
export const coreRolePage = params => { return axios.get('/admin/store/storeUserrole/page', { params: params }) }

// 角色信息获取
export const coreRoleGet = params => { return axios.get('/admin/store/storeUserrole/get', { params: params }) }

// 角色删除
export const coreRoleDelete = params => { return axios.post('/admin/store/storeUserrole/delete', params) }

// 角色添加
export const coreRoleSave = params => { return axios.post('/admin/store/storeUserrole/save', params) }

// 角色修改
export const coreRoleUpdate = params => { return axios.post('/admin/store/storeUserrole/update', params) }

// 获取成员列表
export const getUserRoleList = params => { return axios.get('/admin/store/storeUserrole/getUser', { params: params }) }

// 移除角色组里的用户
export const removeUserRole = params => { return axios.post('/admin/store/storeUserrole/remove', params) }



