// 店铺管理相关接口
import axios from './../fetch'

// 店铺列表分页
export const shopManageList = params => { return axios.get('/admin/store/store/page', { params: params }) }

// 根据id获取单个店铺列表
export const shopManageGet = params => { return axios.get('/admin/store/store/get', { params: params }) }

// 新增店铺
export const shopManageSave = params => { return axios.post('/admin/store/store/save', params) }

// 修改店铺
export const shopManageUpdate = params => { return axios.post('/admin/store/store/update', params) }

// 删除店铺
export const shopManageDelete = params => { return axios.post('/admin/store/store/delete', params) }

// 验证店铺名
export const validName = params => { return axios.get('/admin/store/store/validName', { params: params }) }

// 验证商家账号
export const validUsername = params => { return axios.get('/admin/store/store/validUsername', { params: params }) }

// 验证手机联系人
export const validPhone = params => { return axios.get('/admin/store/store/validPhone', { params: params }) }

// 验证邮箱
export const validContactMail = params => { return axios.get('/admin/store/store/validContactMail', { params: params }) }

// 验证身份证
export const validIdcard = params => { return axios.get('/admin/store/store/validIdcard', { params: params }) }

// 等级申请列表分页
export const shopLevelList = params => { return axios.get('/admin/store/store/levPage', { params: params }) }

// 删除店铺申请等级
export const shopLevelDelete = params => { return axios.post('/admin/store/store/delLevel', params) }

// 查看店铺商品分类
export const categorylist = params => { return axios.get('/admin/store/storeproductcategory/list', { params: params }) }
// 查看店铺商品分类树形结构
export const storeCategoryTree = params => { return axios.get('/admin/store/storeproductcategory/tree', { params: params }) }
// 新增店铺下级分类
export const savecategory = params => { return axios.post('/admin/store/storeproductcategory/save', params) }
// 获取店铺商品详情
export const categoryGet = params => { return axios.get('/admin/store/storeproductcategory/get', { params: params }) }
// 修改店铺商品分类
export const categoryUpdate = params => { return axios.post('/admin/store/storeproductcategory/update', params) }
// 删除店铺商品分类
export const categoryDelete = params => { return axios.post('/admin/store/storeproductcategory/delete', params) }

// 异步获取主营分类列表
export const productCategoryTree = params => { return axios.get('/admin/store/productcategory/tree', { params: params }) }
// 获取会员信息
export const memberList = params => { return axios.get('/admin/store/member/page', { params: params }) }

// 获取子节点数据集
export const artCategoryGetChild = params => { return axios.get('/admin/store/articlecategory/getChild', { params: params }) }
// 获取保障服务信息
export const storeServiceList = params => { return axios.get('/admin/store/storebonddetail/list', { params: params }) }




