<template>
  <div class="cms-list-layout"  id="printPage">
    <!--startprint-->
    <div style="width:210mm;margin:0 auto;">
    <div class="print-wrap"  style=" width: 100%;border: 1px dotted #e0e0e0;min-height:400px;font-size:14px;">
      <h1 style="text-align:center;">{{orderInfo.store.storeName}}</h1>
      <section style=" display: flex;justify-content:space-between;flex-wrap:wrap;padding: 10px;">
        <p style="width:40%;"><label>收货地址:</label><span>{{orderInfo.orderExt.customerAddress}}</span></p>
        <p style="width:40%;"><label>联系电话:</label><span>{{orderInfo.orderExt.customerTel}}</span></p>
        <p style="width:40%;margin-top:2px;"><label>订单号:</label><span>{{orderInfo.orderCode}}</span></p>
        <p style="width:40%;margin-top:2px;"><label>下单时间:</label><time>{{$moment(orderInfo.orderTime)}}</time></p>
      </section>
      <div style="padding:0 10px;">
        <table style="width:100%;text-align:center;border-bottom:2px solid #797979;padding-bottom:5px;font-size:14px;">
          <tr style="border:2px solid #797979;border-left:0;border-right:0;">
            <th style="padding:5px 0;">序号</th>
            <th>商品</th>
            <th>单价</th>
            <th>数量</th>
            <th>小计</th>
          </tr>
          <tr v-for="(item,index) in goodsInfo" :key="index" >
            <td style="padding:5px 0;">{{index+1}}</td>
            <td style="text-align:left;padding-left:5px;width:60%;">{{item.productInfo.productName}}</td>
            <td>￥{{$cms.float(item.productPrice)}}</td>
            <td>{{item.productCount}}</td>
            <td>￥{{$cms.float(item.productPrice*item.productCount)}}</td>
          </tr>
        </table>
      </div>
      <div style="display: flex;justify-content: space-between;padding: 10px;font-size:14px;">
        <p style="width:25%;">订单总额:￥{{$cms.float(orderInfo.orderTotalAmount)}}</p>
        <p style="width:25%;">运费:￥{{$cms.float(orderInfo.orderFreight)}}</p>
        <p style="width:25%;">优惠:￥{{$cms.float(orderInfo.platformDiscount)}}</p>
        <p style="width:25%;">实付款:￥{{$cms.float(orderInfo.orderPayAmount)}}</p>
      </div>
    </div>
   <!--endprint-->
    <div style="text-align:center;margin-top:40px;" class="print-botton">
      <button  onclick="javascript:window.print()"
      style="display:inline-block;width:80px;height:30px;cursor:pointer;
      background-color:#409EFF;color:white;border-radius:5px;outline:0;border:1px solid #409EFF">
      打印
      </button>
    </div>
      </div>

  </div>
</template>

<script>
import { orderGet } from './../../../api/shop/order.js'
export default {
  data() {
    return {
      orderInfo: {
        store: {},
        orderExt: {}
      }, // 订单的信息
      goodsInfo: [], // 商品的信息
      loading: false
    }
  },
  methods: {
    getOrderInfo(id) {
      return new Promise(resolve => {
        // 获取订单信息
        orderGet({ id: id }).then(res => {
          if (res.code === 200) {
            this.orderInfo = res.data
            this.goodsInfo = res.data.items
            this.loading = false
            this.$nextTick(() => {
              resolve(this.$el)
            })
          } else {
            this.$errorMessage(res.message)
          }
        })
      })
    },
    print() {
      // 1.设置要打印的区域 div的className
      var newstr = document.getElementsByClassName('print-wrap')[0].innerHTML
      // 2. 复制给body，并执行window.print打印功能
      document.body.innerHTML = newstr
      // 3. 还原：将旧的页面储存起来，当打印完成后返给给页面。
      const page = window.open('', 'print')
      page.document.body.innerHTML = newstr
    }
  },
  created() {
  }
}
</script>

<style scoped>

</style>



