// 运费模板相关接口
import axios from './../fetch'

// 运费列表所有
export const freightList = params => { return axios.get('/admin/store/storeexpresstpl/list', { params: params }) }
// 运费列表分页
export const freightPage = params => { return axios.get('/admin/store/storeexpresstpl/page', { params: params }) }

// 增加运费
export const freightSave = params => { return axios.post('/admin/store/storeexpresstpl/save', params) }

// 修改运费
export const freightUpdate = params => { return axios.post('/admin/store/storeexpresstpl/update', params) }
// 获取运费
export const freightGet = params => { return axios.get('/admin/store/storeexpresstpl/get', { params: params }) }
// 删除运费模板
export const freightDelete = params => { return axios.post('/admin/store/storeexpresstpl/delete', params) }
// 删除运费模板
export const freightDeleteDetail = params => { return axios.post('/admin/store/storeexpresstpldetail/delete', params) }



