// 订单相关接口
import axios from './../fetch'
// 获取订单列表
export const orderPage = params => { return axios.get('/admin/store/order/page', { params: params }) }
// 获取订单列表
export const updateOrderAmount = params => { return axios.post('/admin/store/order/updateOrderAmount', params) }
// 取消订单
export const cancelOrder = params => { return axios.get('/admin/store/order/cancel', { params: params }) }
// 获取物流信息
export const getFlowInfo = params => { return axios.get('/admin/store/orderflow/info', { params: params }) }
// 获取订单详细
export const orderGet = params => { return axios.get('/admin/store/order/get', { params: params }) }
// 获取售后列表
export const refundPage = params => { return axios.get('/admin/store/refund/page', { params: params }) }
// 获取售后详情
export const refundGet = params => { return axios.get('/admin/store/refund/get', { params: params }) }
// 售后-确认收货
export const refundReceive = params => { return axios.get('/admin/store/refund/receive', { params: params }) }
// 售后-换货-卖家发货
export const refundDeliver = params => { return axios.post('/admin/store/refund/deliver', params) }
// 售后确认操作
export const refundChcek = params => { return axios.post('/admin/store/refund/check', params) }
// 商家收货地址列表
export const storeAddressPage = params => { return axios.get('/admin/store/storeaddress/page', { params: params }) }
// 设置商家收货地址
export const storeAddressSave = params => { return axios.post('/admin/store/storeaddress/save', params) }
// 修改商家收货地址
export const storeAddressUpdate = params => { return axios.post('/admin/store/storeaddress/update', params) }
// 删除商家收货地址
export const storeAddressDelete = params => { return axios.post('/admin/store/storeaddress/delete', params) }
// 设置商家收货默认
export const setStoreAddressDefault = params => { return axios.get('/admin/store/storeaddress/default', { params: params }) }
// 设置商家收货默认
export const setStoreAddressNotDefault = params => { return axios.get('/admin/store/storeaddress/isNotDefault', { params: params }) }
// 商家发货
export const orderDelivery = params => { return axios.post('/admin/store/order/delivery', params) }
// 获取物流公司列表
export const logisticscompanyList = params => { return axios.get('/admin/store/logisticscompany/list', { params: params }) }
// 售后-查询物流信息
export const refundFlowInfo = params => { return axios.get('/admin/store/refund/refundFlowInfo', { params: params }) }




