// 文章分类相关接口
import axios from '../fetch'
import qs from 'qs'

// 获取结算列表分页
export const storesettlementPage = params => {
  return axios.get('/admin/store/storesettlement/page', { params: params,
    paramsSerializer: function(params) {
      return qs.stringify(params, { arrayFormat: 'repeat' })
    }
  })
}

// 获取结算周期列表
export const storesettlementcycleList = params => { return axios.get('/admin/store/storesettlementcycle/list', { params: params }) }

// 获取结算明细-销售
export const orderSettlementPage = params => { return axios.get('/admin/store/order/settlementPage', { params: params }) }

// 获取结算明细-售后
export const refundSettlementPage = params => { return axios.get('/admin/store/refund/settlementPage', { params: params }) }

// 获取异常列表
export const storesettlementerrorPage = params => { return axios.get('/admin/store/storesettlementerror/page', { params: params }) }

// 获取异常详情
export const storesettlementerrorGet = params => { return axios.get('/admin/store/storesettlementerror/get', { params: params }) }

// 模糊查询订单
export const getOrderNumber = params => { return axios.get('/admin/store/storesettlementerror/getOrderNumber', { params: params }) }

// 查询订单是否存在结算
export const checkBusinessNumber = params => { return axios.get('/admin/store/storesettlementerror/checkBusinessNumber', { params: params }) }

// 异常金额汇总
export const storesettlementerrorTotalInfo = params => { return axios.get('/admin/store/storesettlementerror/totalInfo', { params: params }) }

// 审核
export const storesettlementExamine = params => { return axios.post('/admin/store/storesettlement/examine', params) }

// 添加异常数据
export const storesettlementerrorSave = params => { return axios.post('/admin/store/storesettlementerror/save', params) }

// 修改异常数据
export const storesettlementerrorUpdate = params => { return axios.post('/admin/store/storesettlementerror/update', params) }

// 删除异常数据
export const storesettlementerrorDelete = params => { return axios.post('/admin/store/storesettlementerror/delete', params) }

// 导出
export const storesettlementExport = params => {
  return axios.get('/admin/store/storesettlement/export', { params: params,
    paramsSerializer: function(params) {
      return qs.stringify(params, { arrayFormat: 'repeat' })
    },
    responseType: 'blob'
  })
}

// 售后导出
export const refundsettlementExport = params => {
  return axios.get('/admin/store/refund/settlementexport',
    { params: params,
      responseType: 'blob' })
}

// 销售单导出
export const orderExport = params => { return axios.get('/admin/store/order/export', { params: params }) }




