// 商城-管理员相关接口
import axios from './../fetch'

// 管理员列表
export const adminList = params => { return axios.get('/admin/store/storeuser/page', { params: params }) }

// 管理员详情
export const adminGet = params => { return axios.get('/admin/store/storeuser/get', { params: params }) }

// 管理员删除
export const adminDelete = params => { return axios.post('/admin/store/storeuser/delete', params) }

// 禁用管理员
export const adminDisable = params => { return axios.get('/admin/store/storeuser/disable', { params: params }) }

// 启用管理员
export const adminEnable = params => { return axios.get('/admin/store/storeuser/enable', { params: params }) }

// 新增管理员
export const adminSave = params => { return axios.post('/admin/store/storeuser/save', params) }

// 修改管理员
export const adminUpdate = params => { return axios.post('/admin/store/storeuser/update', params) }

// 邮箱验证
export const adminValidEmail = params => { return axios.get('/admin/store/storeuser/validContactMail', { params: params }) }
// 手机验证
export const adminValidPhone = params => { return axios.get('/admin/store/storeuser/validPhone', { params: params }) }
// 用户名验证
export const adminValidUsername = params => { return axios.get('/admin/store/storeuser/validUsername', { params: params }) }




