
<template>
  <div>
    <el-button @click="initData" size="mini" type="primary">选择商品</el-button>
    <el-dialog title='选择商品' :visible="dialogVisible" width="80%" :before-close="close">
      <div class="cms-dialog__body">
        <section class="cms-list-query">
          <div class="query-group">
            <label>商品名称</label>
            <el-input v-model="queryParams.name" @keyup.native.enter='query'></el-input>
          </div>
          <div class="query-group">
            <label>SPU编号</label>
            <el-input v-model="queryParams.itemCode" @keyup.native.enter='query'></el-input>
          </div>
          <div class="query-group">
            <label>品牌</label>
            <el-input v-model="queryParams.userName" @keyup.native.enter='query'></el-input>
          </div>
          <div class="query-group">
            <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
          </div>
        </section>
        <el-table v-loading='loading' element-loading-text="正在加载..." :data="tableData" style="cursor: pointer;" 
        ref='dataTable'
        @selection-change="getSelect"
        @row-click="checkRows"
        row-key="product.id"
         height='350'>
          <!-- 多选 -->
          <el-table-column min-width="5%" type="selection"
          :reserve-selection="true"
          >
          </el-table-column>
          <!-- <el-table-column prop="product.itemCode" label="SPU编号" min-width="35%"></el-table-column> -->
          <el-table-column prop="product.name" label="商品名称" class-name='is-height'>
            <template slot-scope="scope">
              <div class="product-table">
                <div class="product-img">
                  <img :src="showProductImg(scope.row.product.resources)" :alt="scope.row.name">
                </div>
                <div class="product-desc">
                  <div class="product-name">
                    {{scope.row.product.name}}
                  </div>
                  <div class="product-attr">
                    <span v-if="scope.row.product.brand">品牌： {{scope.row.product.brand.name}}</span>
                    <span>货号： {{scope.row.product.itemNumber}}</span>
                  </div>
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="分类名称" min-width="25%" class-name='is-height'>
            <template slot-scope="scope">
              <div class="product-brand">
                <div class="brand-title">{{scope.row.product.category.cateName}}</div>
                <div class="brand-category" v-if="scope.row.product.category.parent">- {{scope.row.product.category.parent.cateName}}</div>
                <div class="brand-category" v-if="scope.row.product.category.parent&&scope.row.product.category.parent.parent">- {{scope.row.product.category.parent.parent.cateName}}</div>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="是否满足活动条件" min-width="25%" class-name='is-height' v-if="type=='promote'">
            <template slot-scope="scope">
              <span class="success" v-if="scope.row.allowSign">是</span>
              <span class="error" v-else>否</span>
            </template>
          </el-table-column>
          <el-table-column label="价格/库存/运费" min-width="25%" class-name='is-height'>
            <template slot-scope="scope">
              <div class="product-price-box">
                <div>
                  <span class="tag">价格:</span>
                  <span class="tag-val">{{scope.row.product.salePrice}}</span>
                </div>
                <div>
                  <span class="tag">库存:</span>
                  <span class="tag-val">{{scope.row.product.inventory}}双</span>
                </div>
                <div>
                  <span class="tag">运费</span>
                  <span class="tag-val">固定运费</span>
                </div>
              </div>
            </template>
          </el-table-column>
        </el-table>
        <cms-pagination @change="getPage" :total='total' ></cms-pagination>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" size="mini" @click="determine">确 定</el-button>
        <el-button type='info' size="mini" @click="close">取 消</el-button>
      </span>
    </el-dialog>
    <ul class="prodSearch-intro-ul clearfix" v-if="show">
      <li class="prodSearch-intro-li" v-for="(item,index) in showItems" :key="item.id">
        <div class="prodSearch-intro-box">
          <img :src="$cms.filterUrl(item.product.coverImage)" class="prodSearch-intro-box__img">
          <p class="prodSearch-intro-box__descript">
              {{item.product.name}}
          </p>
          <p class="prodSearch-intro-box__price error">
            ￥{{$cms.float(item.product.salePrice)}}
           </p>
        </div>
         <div class="prodSearch-intro-box__cancel" @click="deleteCoupon(index)">
           取消
         </div>
      </li>
    </ul>
  </div>
</template>

<script>
import { productPageByPromote } from '@/api/marketing/index.js'
import { productPage } from '@/api/product/product.js'
import list from '@/mixins/list'
export default {
  mixins: [list],
  name: 'cmsCoupon',
  props: {
    value: {},
    multiple: {
      type: Boolean,
      default: true
    },
    type: {
      type: String,
      default: 'promote'
    },
    promoteId: {
      type: [Number, String],
      default: 0
    },
    labelName: {
      type: String,
      default: 'name'
    },
    data: {
      type: Array,
      default: function() {
        return []
      }
    },
    show: {
      type: Boolean,
      default: true
    }
  },
  watch: {
    data: {
      handler() {
        this.showItems = this.$cms.deepCopy(this.data)
        this.$emit('get', this.data)
        this.$emit('input', this.data.map(v => v.product.id))
      },
      immediate: true,
      deep: true
    }
  },
  data() {
    return {
      dialogVisible: false,
      checkItems: [],
      queryParams: {
        page: 1,
        size: 10,
        promoteId: this.promoteId,
        status: 3
      },
      showItems: [] // 组件内显示的数组
    }
  },
  methods: {
    getSelect(selection) {
      this.checkItems = selection
    },
    getTableData(callback, params, data) {
      // 获取表格数据,处理翻页选中
      this.tableData = []
      this.loading = true
      this.callback = callback
      this.isPage = true
      setTimeout(() => {
        callback(params)
          .then(res => {
            if (res.code === 200) {
              this.total = res.data.totalElements
              if (this.type === 'promote') {
                this.tableData = res.data.content
              } else {
                this.tableData = []
                res.data.content.map((v, i) => {
                  const obj = {
                    product: v
                  }
                  this.tableData.push(obj)
                })
              }
              // 不做回填
              // this.defaultToggleSelection()
            } else {
              this.$errorMessage(res.message)
            }
            this.loading = false
          })
          .catch(res => {
            this.loading = false
            // this.$errorMessage('加载失败，请重试！')
          })
      }, 1)
    },
    initData() {
      this.dialogVisible = true
      if (this.type === 'promote') {
        this.getTableData(productPageByPromote, this.queryParams, this.data)
      } else {
        this.getTableData(productPage, this.queryParams, this.data)
      }
    },
    defaultToggleSelection() {
      // 处理回填的默认勾选
      this.$refs.dataTable.clearSelection()
      // console.log(this.showItems)
      this.$nextTick(() => {
        this.tableData.map((v, index) => {
          for (let i = 0; i < this.showItems.length; i++) {
            if (v.product.id === this.showItems[i].product.id) {
              this.$refs.dataTable.toggleRowSelection(
                this.tableData[index],
                true
              )
              break
            }
          }
        })
      })
    },
    checkRows(row) { // 获取行点击
      this.$refs.dataTable.toggleRowSelection(row)
    },
    determine() { // 必须点击确定才能变更数据
      // 构造显示的列表数据
      const arr = []
      this.checkItems.forEach((v, i) => {
        const obj = {
          product: {
            id: v.product.id || '',
            coverImage: v.product.resources[0].imageResouce.url || '',
            name: v.product.name || '',
            salePrice: v.product.salePrice || 0
          }
        }
        arr.push(obj)
      })
      const addArr = []
      arr.forEach(v => {
        let flag = true
        for (let i = 0; i < this.showItems.length; i++) {
          if (v.product.id === this.showItems[i].product.id) {
            flag = false
            break
          }
        }
        if (flag) {
          addArr.push(v)
        }
      })
      this.showItems = this.showItems.concat(addArr)
      this.$emit('get', this.showItems)
      this.$emit('input', this.showItems.map(v => v.product.id))

      this.close()
    },
    deleteCoupon(index) {
      this.showItems.splice(index, 1)
      this.checkItems.splice(index, 1)
      if (this.$refs.dataTable) {
        this.$refs.dataTable.clearSelection()
      }
      this.$emit('get', this.showItems)
      this.$emit('input', this.showItems.map(v => v.product.id))
    },
    close(done) {
      this.dialogVisible = false
      this.$refs.dataTable.clearSelection()
    },
    showProductImg(data) {
      // 显示主图
      if (data.length > 0) {
        const item = data.filter(v => v.isCover === true)
        if (item[0].imageResouce) {
          return this.$cms.filterUrl(item[0].imageResouce.url)
        } else {
          return '/static/error.png'
        }
      } else {
        return '/static/error.png'
      }
    }
  }
}
</script>

<style lang='scss' scoped>
/* 商品搜索 */

/* 商品搜索主体 */
.prodSearch-intro {
  position: relative;
  font-size: 14px;
  overflow: hidden;
}
.prodSearch-intro-li {
  cursor: pointer;
  float: left;
  width: 150px;
  height: 180px;
  border: 1px solid #e0e0e0;
  padding: 10px 10px 0 10px;
  margin-top: 10px;
  border-radius: 4px;
  margin-right: 15px;
  position: relative;
}
.prodSearch-intro-box {
  &__img {
    width: 80px;
    height: 80px;
    display: block;
    margin: 0px auto 5px auto;
  }
  &__price {
    text-align: center;
    font-size: 14px;
    color: #f56c6c;
    margin-bottom: 10px;
  }
  &__descript {
    text-align: center;
   font-size: 14px;
    color: #444;
    line-height: 20px;
    height: 40px;
    overflow: hidden;
    -webkit-line-clamp: 2;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
  }
  &__cancel{
      display: none;
     height: 32px;
     background: rgba(0,0,0,.6);
     color: #fff;
     line-height: 32px;
     font-size: 14px;
     text-align: center;
     position: absolute;
     bottom: 0;
     left: 0;
     width: 100%;
  }
}
.prodSearch-intro-li:hover{
  .prodSearch-intro-box__cancel{
    display: block;
  }
}
</style>



