<template>
  <div>
    <div @click="showPay">
      <slot></slot>
    </div>
    <el-dialog :visible="visible" title="收银台" width="800px" :before-close="cancelPay">
      <div style="padding:20px;" v-loading="payLoading" element-loading-text="正在支付...">
        <h2 v-if="orderNum">订单编号:{{orderNum}}</h2>
        <div class="pay-box">
          <!-- 余额支付 -->
          <div v-if="payScene!=='storeRecharge'">
            <h2 class="pay-title" :style="{backgroundColor:color}">余额支付</h2>
            <el-checkbox v-model="payType" :true-label="4" :false-label="0">余额支付</el-checkbox>
          </div>
          <!-- 在线支付 -->
          <div>
            <h2 class="pay-title" :style="{backgroundColor:color}">在线支付</h2>
            <ul class="pay-items" v-if="payTypeLoad">
              <li
              v-if="canUseType.includes(1)"
                class="pay-item"
                @click="setPayType(1)"
                :style="payType===1?{borderColor:color}:{}"
              >
                <img src="@/assets/images/pay-ali.jpg" alt class="pay-logo">
              </li>
              <li 
                v-if="canUseType.includes(2)"
                class="pay-item"
                @click="setPayType(2)"
                :style="payType===2?{borderColor:color}:{}"
              >
                <img src="@/assets/images/pay-wx.gif" alt class="pay-logo">
              </li>
              <!-- <li
               v-if="canUseType.includes(3)"
                class="pay-item"
                @click="setPayType(3)"
                :style="payType===3?{borderColor:color}:{}"
              >
                <img src="@/assets/images/pay-union.jpg" alt class="pay-logo">
              </li> -->
            </ul>
          </div>
          <!-- 微信支付 -->
          <div class="pay-cover wx-cover" v-if="wxStatus">
            <div class="pay-img">
              <div class="pay-img__inner" v-loading="wxLoading">
                <img :src="wxPayUrl" alt>
              </div>
              <div class="pw-box-ft">
                <p>请使用微信扫一扫</p>
                <p>扫描二维码支付</p>
              </div>
              <div class="other-pay" :style="{color:color}" @click="otherPay">
                <span class="el-icon-arrow-left"></span> 选择其他支付方式
              </div>
            </div>
            <div class="p-w-sidebar"></div>
          </div>
          <el-form v-show="payType===4" :model="payParams" ref="form">
            <el-form-item
              class="error-210"
              label="输入支付密码"
              prop="payPassword"
              :rules="[{ required: true, message: '请输入支付密码', trigger: 'blur' }]"
            >
              <el-input
                type="password"
                style="width:295px;"
                v-model="payParams.payPassword"
                :autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-form>
          <div style="margin-top:15px;">
            <el-button @click="pay" type="primary">支付</el-button>
          </div>
        </div>
        <!-- 支付过程中启动遮罩 -->
        <div class="pay-cover ali-cover" v-if="payStatus">
          <h3>请根据第三方支付结果选择对应的操作</h3>
          <el-button type="primary" @click="closePay">支付成功</el-button>
          <el-button type="warning" @click="closePay">支付失败</el-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { getPayInfo, setPay } from './../../api/marketing/index.js'
const random = require('string-random')
const randomStr = random(16)
export default {
  props: {
    // 业务订单号
    orderNum: [String, Number], // 订单号，有些参数可能不需要可以不传
    payScene: [String], // 必填
    bizContent: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  data() {
    return {
      visible: false,
      payTypeLoad: false,
      canUseType: [],
      payType: 1, // 支付方式
      payStatus: false, // 支付过程  正在支付 true 未支付为false
      payLoading: false, // 余额支付遮罩
      wxStatus: false, // 微信支付状态
      wxLoading: true, // 微信过度
      wxPayUrl: '', // 付款码地址
      socket: null, // ws对象
      payParams: {
        subject: '',
        payScene: this.payScene,
        orderNo: this.orderNum,
        payType: this.payType,
        viewType: 'pc',
        payPassword: null,
        bizContent: JSON.stringify(
          Object.assign({ websocketSessionId: randomStr }, this.bizContent)
        )
      }
    }
  },
  computed: {
    color() {
      return this.$store.state.system.color
    }
  },
  methods: {
    showPay() {
      this.visible = true
    },
    cancelPay() {
      if (this.wxStatus || this.payStatus) {
        this.$successMessage('正在支付....')
      } else {
        this.visible = false
      }
    },
    closePay() {
      this.socket = null
      this.payStatus = false
      this.visible = false
      this.$emit('change', true)
    },
    getPayType() {
      // 获取开通了的支付方式
      getPayInfo().then(res => {
        if (res.code === 200) {
          this.canUseType = res.data.otherOnlinePay.map(v => {
            if (v.typeVal) {
              return v.typeVal
            }
          })
        }

        this.payTypeLoad = true
      })
    },
    setPayType(type) {
      this.payType = type
    },
    otherPay() {
      // 选择其他支付方式
      if (this.socket) {
        this.socket.close() // 关闭连接
      }
      this.wxStatus = false
      this.wxPayUrl = ''
      this.wxLoading = true
    },
    pay() {
      // 调用支付
      const params = Object.assign({}, this.payParams)
      params.payType = this.payType
      if (this.payType === 4) {
        // 余额支付
        this.$refs.form.validate(valid => {
          if (valid) {
            params.payPassword = this.$cms.encrypt(this.payParams.payPassword)
            this.payLoading = true
            setPay(params).then(res => {
              this.payLoading = false
              if (res.code === 200) {
                this.balancePay(res)
              } else {
                this.$errorMessage(res.message)
              }
            })
          }
        })
      } else {
        // 在线支付
        setPay(params).then(res => {
          if (res.code === 200) {
            switch (this.payType) {
              case 1: // 支付宝
                this.aliPay(res)
                break
              case 2: // 微信
                this.wxPay(res)
                break
              case 3: // 银联
                this.unionPay(res)
                break
              default:
                return false
            }
          } else {
            this.wxLoading = false
            this.$errorMessage(res.message)
          }
        })
      }
    },
    wxPay(res) {
      this.wxPayUrl = ''
      this.wxStatus = true
      this.wxLoading = true
      if (res.data.payBody !== '') {
        // 启动ws构造支付遮罩
        this.wxPayUrl =
          process.env.baseUrl +
          '/common/qrcode/800?val=' +
          encodeURI(res.data.payBody)
        this.wxLoading = false
        this.startSocket()
      } else {
        this.wxLoading = false
        this.$errorMessage('订单生成失败')
      }
    },
    aliPay(res) {
      if (res.data.payBody !== '') {
        // 构造支付宝提交订单
        const form = res.data.payBody.replace(
          /<script\b[^<]*(?:(?!<\/script>)<[^<]*)*<\/script>/gi,
          ''
        )
        const div = document.createElement('div')
        div.innerHTML = form
        document
          .querySelector('body')
          .insertBefore(div, document.querySelector('#app'))
        document
          .getElementsByName('punchout_form')[0]
          .setAttribute('target', '_blank')
        document.getElementsByName('punchout_form')[0].submit()
        // 启动ws构造支付遮罩
        this.payStatus = true
        this.startSocket()
      } else {
        this.$errorMessage('订单生成失败')
      }
    },
    unionPay(res) {
      // 银联支付
      if (res.data.payBody !== '') {
        // 构造银联提交订单
        var newwindow = window.open('', '_blank', '')
        newwindow.document.write(res.data.payBody)
        // 启动ws构造支付遮罩
        this.payStatus = true
        this.startSocket()
      } else {
        this.$errorMessage('订单生成失败')
      }
    },
    balancePay(res) {
      if (res.data.payBody === 'Successful!') {
        this.$emit('change', true)
        this.visible = false
      }
    },
    startSocket() {
      const url = process.env.baseUrl
        .replace('https://', 'wss://')
        .replace('http://', 'ws://')
      const socket = new WebSocket(url + '/websocket/' + randomStr)
      socket.onmessage = res => {
        if (res.data === 'success') {
          this.socket.close()
          this.$emit('change', true)
          this.visible = false
        }
      }
      this.socket = socket
    }
  },
  created() {
    this.getPayType()
  }
}
</script>

<style lang="scss" scoped>
.other-pay {
  margin: 15px 0;
  font-size: 16px;
  cursor: pointer;
}
.p-w-sidebar {
  float: left;
  width: 379px;
  height: 347px;
  padding-left: 50px;
  margin-top: -20px;
  background: url("./../../assets/images/pc_icon_phone-bg.png") 50px 0 no-repeat;
  background-size: contain;
}
.pw-box-ft {
  height: 60px;
  padding: 7px 0 8px 62px;
  background: #ff7674 url("./../../assets/images/pc_icon.png") 10px 8px
    no-repeat;
  p {
    margin: 0;
    font-size: 14px;
    line-height: 22px;
    color: #fff;
    font-weight: 700;
  }
}
.pay-box {
  width: 100%;
  height: 420px;
  padding: 0 20px;
  position: relative;
  overflow: hidden;
}
.wx-cover {
  padding: 30px;
}
.pay-cover {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: #fff;
  color: #333;
  z-index: 9999;
}
.ali-cover {
  text-align: center;
  h3 {
    font-size: 18px;
    margin: 15px 0;
  }
}
.pay-img {
  float: left;
  overflow: hidden;
  margin-left: 55px;
}
.pay-img__inner {
  width: 250px;
  height: 250px;
  border: 1px solid #e0e0e0;
  padding: 20px;
  margin-bottom: 15px;
  img {
    width: 100%;
    height: 100%;
  }
}
.pay-title {
  padding: 5px 0px 5px 10px;
  color: #fff;
  margin: 15px 0;
  position: relative;
  left: -20px;
  width: 90px;
  font-weight: normal;
  border-radius: 0 5px 5px 0;
}
.pay-items {
  overflow: hidden;
}
.pay-item {
  float: left;
  border: 2px solid #eee;
  border-radius: 4px;
  margin-right: 15px;
  cursor: pointer;
  overflow: hidden;
}
.pay-logo {
  width: 120px;
  height: 50px;
}
</style>





