import axios from './../fetch'
// 优惠券列表/admin/store/promote/coupon/page
export const couponPage = params => { return axios.get('/admin/store/promote/coupon/page', { params: params }) }
// 优惠券新增
export const couponSave = params => { return axios.post('/admin/store/promote/coupon/save', params) }
// 优惠券修改
export const couponUpdate = params => { return axios.post('/admin/store/promote/coupon/update', params) }
// 优惠券删除
export const couponDelete = params => { return axios.post('/admin/store/promote/coupon/delete', params) }
// 优惠券获取
export const couponGet = params => { return axios.get('/admin/store/promote/coupon/get', { params: params }) }

// 店铺活动管理列表
export const activityPage = params => { return axios.get('/admin/store/promote/page', { params: params }) }
// 活动管理新增
export const activitySave = params => { return axios.post('/admin/store/promote/save', params) }
// 活动管理修改
export const activityUpdate = params => { return axios.post('/admin/store/promote/update', params) }
// 活动管理删除
export const activityDelete = params => { return axios.post('/admin/store/promote/delete', params) }
// 活动管理获取
export const activityGet = params => { return axios.get('/admin/store/promote/get', { params: params }) }
// 获取收费配置列表
export const promoteConfigList = params => { return axios.get('/admin/store/promote/service/list', { params: params }) }
// 修改收费设置
export const promoteConfigUpdate = params => { return axios.post('/admin/store/promoteConfig/update', params) }
// 购买服务生成订单
export const servicePrepaye = params => { return axios.post('/admin/store/promote/service/prepay', params) }

// 支付购买
export const getPayInfo = params => { return axios.post('/admin/store/getPayInfo', params) }

// zhifu
export const setPay = params => { return axios.post('/admin/store/pay', params) }

// 平台活动管理列表
export const activityPlatformPage = params => { return axios.get('/admin/store/promote/platform/page', { params: params }) }

// 查看平台活动报名详情
export const getActivityPlatformSign = params => { return axios.get('/admin/store/promote/platform/getSign', { params: params }) }

export const activityPlatformPrepay = params => { return axios.get('/admin/store/promote/platform/prepay', { params: params }) }

// 平台活动管理获取
export const activityPlatformGet = params => { return axios.get('/admin/store/promote/platform/get', { params: params }) }
// 活动商品选择列表
export const productPageByPromote = params => { return axios.get('/admin/store/product/getPageByPromote', { params: params }) }
// 平台活动报名
export const signPlatformPromote = params => { return axios.post('/admin/store/promote/platform/sign', params) }
// 平台活动报名
export const signPlatformStoreService = params => { return axios.post('/admin/store/promote/service/storeService', params) }



